import DR from "../Generic/DR";
import { useState, useCallback } from "react";
import { UR, LG } from "../JSON/UR";
import "../Generic/Dr.css";
import Avatar from "@mui/material/Avatar";
const Dr = () => {
  const {
    dfltCountryCode,
    // dfltCountryName,
    // dfltWhsGroup,
    roles
    // isAdmin,
    // isSuperUser
  } = UR;
  const cd = roles.map((i) => ({ label: i.countryName, value: i.countryCode }));
  let ws = roles.find((i) => i.countryCode === dfltCountryCode).whsGroup;
  ws = Object.entries(ws).map((i) => ({
    label: i[1].split(":")[0],
    value: i[1].split(":")[1]
  }));
  const lng = LG.filter((i) => i.value === dfltCountryCode || i.value === "EN");
  const [cnc, scnd] = useState(cd[0].value);
  const [lang, slang] = useState(lng[0].value);
  const [wsc, swsc] = useState(ws[0].value);

  const cnCh = useCallback((e) => {
    scnd(e.value);
  }, cnc);
  const lgCh = useCallback((e) => {
    slang(e.value);
  }, lang);
  const wsCh = useCallback((e) => {
    swsc(e.value);
  }, wsc);

  return (
    <div id="dr" className="dr">
      <div className="cn">
        <Avatar className="hd_ic" />
        <DR
          options={cd}
          defaultOption={cnc}
          onChange={(e) => cnCh(e)}
          placeholder="Country"
        />
      </div>
      <div className="lg">
        <DR
          options={lng}
          defaultOption={lang}
          onChange={(e) => lgCh(e)}
          placeholder="Language"
        />
      </div>
      <div className="ws">
        <DR
          options={ws}
          defaultOption={wsc}
          onChange={(e) => wsCh(e)}
          placeholder="Wholesaler"
        />
      </div>
    </div>
  );
};
export default Dr;
